@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.1.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,300,700');

body {
  color: #b6b5ca;
  font-family: 'Arial', sans-serif;
  margin: 0;
  text-align: center;
}

#board {
  display: flex;
  flex-wrap: wrap;
  height: 300px;
  overflow: hidden;
  margin: 50px auto 30px auto;
  position: relative;
  width: 300px;
}

@media (min-width: 450px) {
  #board {
    height: 450px;
    width: 450px;
  }
}

.square {
  background-color: #d1ccc0;
  border-radius: 10px;
  cursor: pointer;
  color: #fff;
  height: 90px;
  margin: 5px;
  width: 90px;
  -moz-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  position: relative;
}

@media (min-width: 450px) {
  .square {
    height: 140px;
    width: 140px;
  }
}

.player-1 .square:hover {
  background-color: #474787;
}

.player-2 .square:hover {
  background-color: #cc8e35;
}

.square.player-1,
.square.selected.player-1 {
  background-color: #706fd3;
}

.square.player-2,
.square.selected.player-2 {
  background-color: #ffb142;
}

.square:hover:after {
  opacity: 0.4;
}

.square:after {
  left: 0;
  font-family: 'FontAwesome';
  font-size: 45px;
  margin-top: -22.5px;
  opacity: 0;
  position: absolute;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  top: 50%;
  width: 100%;
}

@media (min-width: 450px) {
  .square:after {
    font-size: 60px;
    margin-top: -30px;
  }
}

.player-1 .square:after,
.square.selected.player-1:after {
  content: '';
}

.player-2 .square:after,
.square.selected.player-2:after {
  content: '';
}

.square.selected.player-1:after,
.square.selected.player-2:after {
  opacity: 1;
}

#game {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#status {
  background: rgba(255, 255, 255, 0.8);
  bottom: 5px;
  color: #3d4250;
  display: none;
  left: 5px;
  padding-top: 55px;
  position: absolute;
  right: 5px;
  top: 5px;
  text-align: center;
  z-index: 11;
}

#status.visible {
  display: block;
}

@media (min-width: 450px) {
  #status {
    padding-top: 110px;
  }
}

#status h3 {
  font-size: 30px;
  font-weight: 300;
}

@media (min-width: 450px) {
  #status h3 {
    font-size: 40px;
  }
}

#status a {
  background-color: #333333;
  border-radius: 4px;
  color: #fff;
  padding: 14px 45px;
  text-decoration: none;
  -moz-transition: background-color 0.2s;
  -o-transition: background-color 0.2s;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

#status a:hover {
  background-color: #333333;
  cursor: pointer;
}
